.container{
  display: flex;
  justify-content: center;
  height: 100%;
}

.loader{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.movies{
  display: grid;
  grid-template-columns: repeat(3, minmax(400px, 1fr));
  grid-gap:100px;

  width: 80%;
  padding: 50px;
  padding-top: 70px;
  /* border: 1px solid #000; */
}

@media screen and (max-width:1180px){
  .movies{
    grid-template-columns: 1fr;
    width: 100%;
  }
}