.movie{
  margin-bottom: 20px;
  padding: 20px;
  color: #adaeb9;
  font-weight: 300;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 
    0 13px 27px -5px rgba(50,50,93,0.55),
    0 8px 16px -8px rgba(0,0,0,0.3),
    0 -6px 16px -6px rgba(0,0,0,0.055)
  ;
}

.movie img{
  width: 100%;
  max-width: 150px;
  box-shadow: 
    0 30px 60px -12px rgba(50,50,93,0.25),
    0 18px 36px -18px rgba(0,0,0,0.3),
    0 -12px 36px -8px rgba(0,0,0,0.25)
  ;
}

.movie__title,
.movie__year{
  margin: 0;
  font-weight: 300;
}

.movie__title{
  margin-bottom: 5px;
  color: #fff;
  font-size: 24px;
}

.movie__genres{
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0;
  list-style: none;
}

.movie__genres>li,
.movie__year{
  margin-right: 10px;
  font-size: 14px;
}

.movie__summary{
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}